.menu-item {
  min-width: 30%;
  height: 340px;
  flex: 1 1 auto;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid #dbdbdb;
  margin: 0 7.5px 15px;
  overflow: hidden;

  &:hover {
    cursor: pointer;

    & .background-image {
      transform: scale(1.4);
      transition: transform 2s cubic-bezier(0.25, 0.45, 0.45, 0.95);
    }

    & .content {
      opacity: 1;
    }
  }

  &.large {
    height: 450px;
  }

  &:first-child {
    margin-right: 7.5px;
  }

  &:last-child {
    margin-left: 7.5px;
  }

  .background-image {
    width: 100%;
    height: 100%;
    background-size: 100% 114%;
    background-position: center;
  }

  .content {
    height: 100px;
    padding: 0 25px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    border: 1px solid rgb(0, 0, 0);
    background-color: rgb(0, 0, 0);
    opacity: 0.7;
    position: absolute;

    .title {
      font-weight: bold;
      margin: 0 6px 0;
      font-size: 22px;
      color: #ffffff;
    }

    .subtitle {
      font-weight: lighter;
      font-size: 16px;
      color: #fff;
    }
  }
}

@media screen and (max-width: 908px) {
  .menu-item {
    min-width: 100%;
    height: 240px;
    flex: 1 1 auto;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid #dbdbdb;
    margin: 0 7.5px 15px;
    overflow: hidden;
  }
}
